import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Campo_indicador } from '../../../modelo/Campo_indicador'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'campo_indicador-dados-details',   templateUrl: 'campo_indicador_dadosDetails.component.html' }) export class Campo_indicadorDadosDetailsComponent implements OnInit {    campo_indicador: Campo_indicador;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.campo_indicador.Campo_indicador";                 let id = +params['id'];         this.campo_indicador = new Campo_indicador();         this.campo_indicador.cd_campo_indicador = id;         o.objetoJson = this.campo_indicador;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a campo_indicador. '; }      this.campo_indicador = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


